/* Reset default browser styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Global styles */
body {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    color: #1B1E2D;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: bold;
}

.a {
    color: #1B1E2D;
    text-decoration: none;
}

.a:hover {
    color: #FF6978;
}

/* Navigation styles */
.nav {
    background-color: #1B1E2D;
    height: 100vh;
    width: 300px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px;
}

.nav .ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.nav .ul .li .a {
    font-size: 24px;
    font-weight: bold;
    transition: color 0.2s ease-in-out;
}

.nav .ul .li .a:hover {
    color: #FF6978;
}

/* Main content styles */
.main {
    margin-left: 300px;
    padding: 40px;
}

/* Button styles */
.button {
    background-color: #FF6978;
    color: #F5F5F5;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.2s ease-in-out;
}

.button:hover {
    background-color: #F44336;
}

/* Form styles */
.form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.label {
    font-size: 20px;
    font-weight: bold;
}

input[type="text"], textarea {
    border: 2px solid #F5F5F5;
    border-radius: 4px;
    padding: 10px;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
}

input[type="text"]:focus, textarea:focus {
    outline: none;
    border: 2px solid #FF6978;
}

/* Timer styles */
.timer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.timer h1 {
    font-size: 80px;
    font-weight: bold;
    text-align: center;
    color: #FF6978;
}

.timer p {
    font-size: 32px;
    font-weight: bold;
    text-align: center;
    color: #1B1E2D;
}

.timer button {
    font-size: 24px;
    font-weight: bold;
}

/* About styles */
.about {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about h1 {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 40px;
    color: #1B1E2D;
}

.about p {
    font-size: 24px;
    text-align: center;
    color: #1B1E2D;
}

